.players {
  display: flex;

  align-items: flex-start;


  .add-new-player {
    height: 40px;
    width: 140px;
    background: transparent;
    border: transparent;
    color: red;
    cursor: pointer;
  }
  .new-player {
    display: flex;
    flex-direction: column;
    border: 1px solid gainsboro;
    padding: 1px;
    background: mediumaquamarine;

    .new-player-input {
    }

    .new-player-buttons {
      display: flex;
      .new-player-submit {
      }
    }
  }


  .players-list {
    display: flex;
    align-items: baseline;

    .player-item {
      width: 200px;
      display: flex;
      flex-direction: column;
      margin-right: -7px;
      box-shadow: 0 0 3px royalblue;
      position: relative;
      border: 1px solid lavenderblush;
      padding: 10px 5px;

      .player-name {
        background: transparent;
        &.button-select-selected {
          background: inherit;
        }

        span {
          color: yellow;
          mix-blend-mode: difference;
          word-break: break-all;
          max-width: 100%;
          position: absolute;
          top: 0;
          left: 16px;
          font-size: 30px;

        }
      }


      .player-ate {
        word-break: break-all;
        ::selection { background: yellow; }
        max-width: 300px;
        flex-wrap: wrap;
        & > div {
          margin-right: 10px;
          display: inline;
        }
      }

      .player-delete {
        display: none;
        position: absolute;
        top: 0;
        right: 0;

        background: transparent;
        border: none;

        cursor: pointer;
        font-size: 13px;
        //font-weight: bold;
        color: gainsboro;


        &:hover {
          color: red;
        }


      }
      &:hover .player-delete {
        display: block;
      }


    }
  }

}

