.dishes {
  display: flex;
  align-items: flex-start;

  .dish-open-form {
    background: transparent;
    color: red;
    border-color: transparent;
    width: 140px;
    height: 140px;
    cursor: pointer;
  }

  .dish-form {
    border: 1px solid tan;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    background: teal;
    padding: 1px;


    .dish-merz {
      display: flex;


      .merz-value {
        font-size: 20px;
        color: whitesmoke;
      }
      .button-number {

        background: url(./images/ttt0000.jpg);
        background-size: 70px;
      }
      .button-number-value {
        background: url(./images/pasta.jpg)  -6px -20px;
        background-size: 300px;
      }
    }


    .dish-name-input {
      display: flex;

      width: 134px;

      font-size: 20px;
      font-family: "Times New Roman";
      height: 20px;

      //border: 4px solid red;
      //border-bottom: 4px solid darkred;
      //border-right: 4px solid darkred;
    }

    .row1 {
      display: flex;

      .dish-props {
        .dish-prop {
          display: flex;
          border: 1px solid lightgrey;
        }

        .dish-prop-name {
          display: flex;
          border: 1px solid lightgrey;
        }
      }

      .dish-submit {
        display: flex;
        border: 1px solid lightgrey;
        background: url(./images/pomidor.jpg)  -6px -20px;
        //height: 20px;
        //background-position: -6px -20px;
        background-size: 79px;
      }
    }

    .row2 {
      display: flex;



    }
  }


  .dishes-list {
    display: flex;
    align-items: baseline;
flex-direction: column;

    .dish-item {
      width: 200px;
      display: flex;
      position: relative;
      color: white;

      background: transparent;
      border-bottom: 1px solid red;
      justify-content: space-between;

      .dish-name {
        display: flex;
        //border: 1px solid lightgrey;
        //background: grey;
      }

      .dish-mark {
        display: flex;
        //border: 1px solid lightgrey;
        //background: grey;
      }

      &:hover .dish-delete-button {
        display: flex;
      }

      .dish-delete-button {
        display: none;

        position: absolute;
        top:0;
        right: 0;
        background: transparent;
        border: none;
        cursor: pointer;
        color: red;

      }
    }

  }
}

