.step-form {
  width: 422px;
  z-index: 10;
  border: 1px solid sienna;
  background: url("./images/pomidor.jpg");
  position: absolute;
  top: calc(100% - 10px);
  display: flex;
  flex-direction: column;

  user-select: none;

  .dishes {
    flex-wrap: wrap;
    .button-select {
      padding: 0;

      border: none;
      color: black;
      font-style: italic;
      //font: 15px "Times New Roman" ;

      overflow: hidden;
      background: black;

      position: relative;


      span {
        position: absolute;
        width: 100%;
        top:0;
        left: 0;
        text-align: left;
        & > span {
          right: 0;
          font-size: 20px;
          mix-blend-mode: difference;
          color: greenyellow;
          text-align: right;
        }
      }

      div {
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;
        left: 0;
        text-align: left;
        background: black;

        & > span {
          position: absolute;
          right: 0;
          top:0;
          font-size: 20px;
          mix-blend-mode: difference;
          color: greenyellow;
          text-align: right;
        }
      }

      &:active  {
        border: 10px solid black;
        position: relative;
        overflow: visible;
        img, span, div {
          color: red;
          position: absolute;
          top: -200px;
          left: -200px;
          width: 400px;
          height: 400px;
          z-index: 100;
        }

        div {
          background: black;
        }

        div, span {
          text-align: left;
          display: flex;
          align-items: flex-end;
          justify-content: flex-start;
          font-size: 70px;
          //background: black;
        }
      }

      &.button-select-selected {
        color: black;

        border: 2px solid red;


        span {


          font-family: 'Arial black';
          //color: powderblue;
          font-weight: bold;
          //position: absolute;
          //top:0;
          //left:0;
        }
      }
    }
  }

  .dops {
    display: flex;
    .dop-button {
      padding: 0;
      position: relative;

      &:active  {
        border: 10px solid black;
        position: relative;
        overflow: visible;
        img, span, div {
          color: red;
          position: absolute;
          top: -200px;
          left: -200px;
          width: 400px;
          height: 400px;
          z-index: 100;
        }

        div {
          background: black;
        }

        div, span {
          text-align: left;
          display: flex;
          align-items: flex-end;
          justify-content: flex-start;
          font-size: 70px;
          //background: black;
        }
      }

      span {
        position: absolute;
        width: 100%;
        top:0;
        left: 0;
        text-align: left;
        & > span {
          right: 0;
          font-size: 20px;
          mix-blend-mode: difference;
          color: greenyellow;
          text-align: right;
        }
      }

      div {
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;
        left: 0;
        text-align: left;
        background: black;

        & > span {
          position: absolute;
          right: 0;
          top:0;
          font-size: 20px;
          mix-blend-mode: difference;
          color: greenyellow;
          text-align: right;
        }
      }

    }
  }

  .dish-props {
    display: flex;

    .dish-prop-item {
      display: flex;
      flex-direction: column;

      .dish-prop-item-title {
        margin-bottom: -5px;
        z-index: 10;
        font-weight: bold;


        color: cornflowerblue;
        mix-blend-mode: color-dodge;


      }

      .button-number {

        background: darkolivegreen;
      }

      .button-number-value {
        background: url(./images/cb.jpg);
        background-size: 100% 100%;
        color: yellow;
      }


    }


  }


  .dish-merz {
    background: url(./images/blu.gif);
    background-size: 50% 50%;

    .button-number {

      background: url(./images/ttt0000.jpg);
      background-repeat: no-repeat;
      background-size: 100% 100%;

      .button-number-value {
        background: url(./images/sosi.png);
        background-size: 84px 50%;
      }


    }

  }

  .submit {
    z-index: 100;
    background: url(./images/pomidor.jpg);
    background-size: 100% 100%;
    &:disabled {
      filter: grayscale(100%);
    }
  }
}