.report-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,.8);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  z-index: 100;
  .report {
    pointer-events: visible;
    //border: 1px solid red;
    width: 100%;
    overflow: hidden;
    background: white;


    table {
      //border: 1px solid blue;

      tr {

        td {
          text-align: left;
          vertical-align: top;
          /* border: 1px solid coral; */
          white-space: nowrap;
          color: red;
          background: radial-gradient(black, transparent);
        }
      }
    }

    .report-dishes {

    }
  }
}